@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,300;0,500;0,900;1,300&family=Reenie+Beanie&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;700&display=swap'); */

:root{
  --main: #641111;
  --accent: #FF9300;
}

html, body, h1, h2, h3, h4, p, div, article, section, a, button {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  color: #641111;
  color: var(--main);
  font-family: -apple-system, BlinkMacSystemFont, 'Fira Sans', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F8FFF1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Layout_Content__3iKlb {
    margin-top: 1em;
}
.Ingredient_BurgerIngredients__1KEoA{
    width: 100%;
    margin: -3px auto;
}





.Burger_Burger__tIaG0 {
  box-sizing: border-box;
  text-align: center;
  min-height: 680px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5em;
  margin-right: 2em;
  background-image: url(/burger-bulider/static/media/background.a5915259.svg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: -10;
}

.Burger_Shadow__1QwLw {
  margin-top: 2em;
}

@media (max-width: 1250px){
    .Burger_Burger__tIaG0{
        padding: 10%;
        min-height: auto;
        margin-top: 5%;
    }
}

@media (max-width: 600px){
    .Burger_Burger__tIaG0{
      width: 320px;
        padding: 10%;
        min-height: auto;
        margin: 10% auto -5% auto;
    }
    .Burger_Shadow__1QwLw {
      width: 250px;
    }
}
.Control_Control__1H1VO {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
}

.Control_Control__1H1VO p {
    color: #641111;
    text-align: center;
}

.Control_Control__1H1VO button {
    display: inline-block;
    font: inherit;    
    border: none;
    background-color: #F8FFF1;
    border-radius: 50%;
    cursor: pointer;
    outline: none;
    margin: 0 10px;
}

.Control_Control__1H1VO button:disabled{
    color: #aaa;
    border-color: #ddd;
}

.Control_Selection__rJflz {
    display: flex;
}

.Control_PriceTag__wI4s_ {
    font-size: 0.8em;
}

.Control_Label__3e0Rs {
    margin-top: 0.5em;
    font-weight: 500;
    text-transform: capitalize;
}
.SelectionControls_SelectionControls__3QVek {
  width: 100%;
  margin: 0 auto;
  padding: 2em 0;
  text-align: left;
}

.SelectionControls_TotalPrice__1R9Kw {
  color: var(--accent);
  line-height: 1em;
  text-align: left;
  font-weight: 700;
  font-size: 3em;
  margin-bottom: 3%;
}

.SelectionControls_SelectionContainer__FEwMH {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  grid-gap: 1em;
  gap: 1em;
  margin: 2em 0;
  /* background-color: red; */
}

.SelectionControls_Warning__141aI {
  display: inline-block;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.25em 1em;
  border-radius: 1em;
  margin-top: 1em;
}

@media (max-width: 950px) {
  .SelectionControls_SelectionContainer__FEwMH {
      width: 100%;
      grid-gap: 1.5em;
      gap: 1.5em;
    grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  }
}

.BurgerBuilder_BurgerBuilder__1cJs0 {
  width: 80%;
  max-width: 1100px;
  text-align: left;
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-areas:
    "burger burger burger title"
    "burger burger burger selection"
    "burger burger burger selection";
}

.BurgerBuilder_Burger__1S3mK {
  grid-area: burger;
  /* min-height: 100vh; */
  display: grid;
  align-items: center;
}

.BurgerBuilder_TitleContainer__1wXw_ {
  grid-area: title;
  display: grid;
  align-items: flex-end;
}

.BurgerBuilder_SelectionControls__2bUub {
  grid-area: selection;
}

.BurgerBuilder_Title__C0ydz {
  width: 20%;
  font-size: 3em;
  font-weight: 700;
  line-height: 0.8em;
  margin-bottom: 0.2em;
}

.BurgerBuilder_SubTitle__3HkzW {
  font-size: 3em;
  font-family: "Reenie Beanie", cursive;
  line-height: 1em;
}

.BurgerBuilder_Description__2KlmP {
  font-style: italic;
}

@media (max-width: 1200px) {
  .BurgerBuilder_BurgerBuilder__1cJs0 {
    max-width: 500px;
    grid-template-areas:
      "title"
      "burger"
      "selection";
  }

  .BurgerBuilder_Burger__1S3mK {
    margin-top: -5%;
  }
}

@media (max-width: 500px) {
  .BurgerBuilder_BurgerBuilder__1cJs0 {
    width: 90%;
    max-width: 100%;
    margin: 0 auto;
    background-color: red;
  }

  .BurgerBuilder_Burger__1S3mK {
    margin-top: -5%;
  }
}

